.mainCoverGrid{
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: repeat(4,160px);
}
.mainCover{
    display: grid;
    grid-template-columns: repeat(5,250px);
    grid-template-rows: repeat(5,100px);
}
.mainHeader{
    grid-row: 2;
    grid-column: 2/4;
    font-size: 50px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 80px;
    line-height: 117px;  
    color: #F8F1F1;
    text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.25);
}
.mainSubHeader{
    grid-row: 3;
    grid-column: 2/5;
    padding-top: 50px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
    /* line-height: 53px; */
    color: #F8F1F1;
    text-shadow: 0px 5px 4px rgba(0, 0, 0, 0.25);
}
.mainJoinButton{
    grid-row: 5;
    grid-column: 2;
    align-self: center;
    height: 30px;
    width: 150px;
}